import { Component, DestroyRef, inject, signal } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { RouterModule, Router, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs';
import { TabsModule } from 'primeng/tabs';
import { AdaptiveLayoutComponent } from '../../../shared/components/splitter/adaptive-layout/adaptive-layout.component';

type TabValue = 'profile' | 'notifications';

@Component({
  selector: 'app-profile-container',
  imports: [AdaptiveLayoutComponent, TabsModule, RouterModule],
  templateUrl: './profile-container.component.html',
  styleUrl: './profile-container.component.scss',
})
export class ProfileContainerComponent {
  private readonly destroyRef = inject(DestroyRef);
  private readonly router = inject(Router);
  protected readonly activeTab = signal<TabValue>('profile');

  constructor() {
    this.setupNavigationHandler();
  }

  private setupNavigationHandler(): void {
    this.router.events
      .pipe(
        filter(
          (event): event is NavigationEnd => event instanceof NavigationEnd,
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe({
        next: (event) => this.handleNavigation(event),
      });
  }

  private handleNavigation(_event: NavigationEnd): void {
    this.activeTab.set('profile');
  }
}
