import {
  ChangeDetectorRef,
  Component,
  computed,
  DestroyRef,
  effect,
  inject,
  input,
  OnInit,
  signal,
} from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import {
  createAutocompleteField,
  createDropdownField,
  createPasswordField,
  createTextField,
  FormAction,
  FormFieldConfig,
  GroupValues,
} from '../../../shared/models/form-field.model';
import { DynamicFormComponent } from '../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridComponent } from '../../../shared/components/grid/grid.component';
import {
  AccountsService,
  ClientListItem,
  ClientsService,
  UserProfileResponse,
} from '../../../api';
import { GridColumn } from '../../../shared/models';
import { HttpClient } from '@angular/common/http';
import { UtilsService } from '../../../shared/services/utils.service';
import { CommonModule } from '@angular/common';
import { UtilityService } from '../../../core/services/utility.service';
import { debounceTime, of, Subject, switchMap } from 'rxjs';
import { AuthDataService } from '../../../core/services/auth-data.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  hasProperty,
  isDefined,
  isNullOrUndefined,
  isString,
} from '../../../shared/utils';
import {
  SEARCH_DEBOUNCE_TIME,
  CLIENT_SEARCH_LIMIT,
} from '../../../core/models';

@Component({
  selector: 'app-details',
  imports: [DynamicFormComponent, GridComponent, CommonModule],
  templateUrl: './details.component.html',
  styleUrl: './details.component.scss',
})
export class DetailsComponent implements OnInit {
  userProfile = input<UserProfileResponse | null>();
  private readonly http = inject(HttpClient);
  private readonly utilsService = inject(UtilsService);
  private readonly utilityService = inject(UtilityService);
  private readonly clientsService = inject(ClientsService);
  private readonly authDataService = inject(AuthDataService);
  private readonly accountsService = inject(AccountsService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  private readonly searchSubject = new Subject<string>();
  formFields: FormFieldConfig[] = [];
  readonly userDetails = computed(() => {
    const profile = this.userProfile();
    if (isNullOrUndefined(profile)) return [];

    return [
      {
        key: 'Name',
        value: isString(profile.name) ? profile.name : 'N/A',
      },
      {
        key: 'Email Address',
        value: isString(profile.emailAddress) ? profile.emailAddress : 'N/A',
      },
      {
        key: 'Mobile Number',
        value: isString(profile.mobile) ? profile.mobile.trim() : 'None',
      },
      {
        key: 'Password',
        value: '********',
      },
      {
        key: 'Time Zone',
        value: isString(profile.timeZoneId) ? profile.timeZoneId : 'N/A',
      },
      {
        key: 'Language',
        value: this.getLanguageName(profile.language),
      },
      {
        key: 'Default Client',
        value:
          isDefined(profile.defaultClient) &&
          hasProperty(profile.defaultClient, 'name')
            ? profile.defaultClient.name
            : 'None',
      },
    ];
  });
  protected readonly isFormEditable = signal<boolean>(false);
  protected readonly isSubmitting = signal<boolean>(false);
  clientSearchResults = signal<ClientListItem[]>([]);
  timezones: { label: string; value: string }[] = [];

  readonly languages = this.utilsService.getLanguages();
  readonly columns: GridColumn[] = [
    { field: 'key', header: '', transform: 'uppercase' },
    { field: 'value', header: '' },
  ];

  actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => this.isFormEditable.set(false),
      severity: 'secondary',
    },
    {
      label: 'Save',
      action: (formData: FormGroup) => {
        this.onSubmit(formData);
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => isNullOrUndefined(form) || form.pristine,
    },
  ];

  constructor() {
    effect(() => {
      const profile = this.userProfile();
      if (profile) {
        this.updateFormFields();
      }
    });
  }

  ngOnInit(): void {
    this.loadTimezones();
    this.setupClientSearch();
  }

  private updateFormFields(): void {
    this.formFields = [
      createTextField('name', 'Name', this.userProfile()?.name, {
        name: 'name',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Name is required',
          },
        ],
      }),
      createTextField(
        'emailAddress',
        'Email Address',
        this.userProfile()?.emailAddress,
        {
          name: 'email',
          validations: [
            {
              name: 'email',
              validator: Validators.email,
              message: 'Invalid email',
            },
            {
              name: 'required',
              validator: Validators.required,
              message: 'Email is required',
            },
          ],
        },
      ),
      createTextField(
        'mobile',
        'Mobile Number',
        this.userProfile()?.mobile?.trim(),
        {
          name: 'mobile',
          validations: [
            {
              name: 'pattern',
              validator: Validators.pattern('^[0-9]*$'),
              message: 'Mobile number must contain only numbers',
            },
            {
              name: 'required',
              validator: Validators.required,
              message: 'Mobile number is required',
            },
          ],
        },
      ),
      createPasswordField('password', 'Password', {
        name: 'password',
        validations: [
          {
            name: 'required',
            validator: Validators.required,
            message: 'Password is required',
          },
        ],
      }),
      createDropdownField(
        'timeZoneId',
        'Time Zone',
        this.timezones,
        this.userProfile()?.timeZoneId,
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          placeholder: 'Select timezone',
          name: 'timeZone',
          props: {
            appendTo: 'body',
          },
        },
      ),
      createDropdownField(
        'language',
        'Language',
        this.languages.map((lang) => ({
          label: lang.name,
          value: lang.id,
        })),
        this.userProfile()?.language,
        {
          layout: 'horizontal',
          labelWidth: '1/3',
          placeholder: 'Select language',
          name: 'language',
          props: {
            appendTo: 'body',
          },
        },
      ),
      createAutocompleteField(
        'defaultClient',
        'Default Client',
        this.userProfile()?.defaultClient?.name,
        this.clientSearchResults(),
        this.searchClient.bind(this),
        {
          name: 'defaultClient',
          props: {
            forceSelection: true,
            field: 'name',
            placeholder: 'Search for clients...',
            completeOnFocus: true,
            appendTo: 'body',
          },
          hint: 'Enter a client name.',
        },
      ),
    ];
  }

  private loadTimezones(): void {
    this.http
      .get<{ id: string; name: string }[]>('/assets/docs/timezones.json')
      .subscribe((data) => {
        this.timezones = data
          .map((tz) => ({ label: tz.id, value: tz.id }))
          .sort((a, b) => a.label.localeCompare(b.label));
        this.updateFormFields();
      });
  }

  private getDirtyValues(group: FormGroup): GroupValues {
    return Object.fromEntries(
      Object.entries(group.controls)
        .filter(([_, control]) => control.dirty)
        .map(([key, control]) => [key, control.value]),
    );
  }

  private getLanguageName(languageId?: string | null): string {
    if (!isString(languageId)) return 'N/A';
    const language = this.languages.find(
      (lang) =>
        isDefined(lang) && hasProperty(lang, 'id') && lang.id === languageId,
    );
    return isDefined(language) && hasProperty(language, 'name')
      ? language.name
      : 'N/A';
  }

  onSubmit(formGroup: FormGroup): void {
    const profile = this.userProfile();
    if (isNullOrUndefined(profile)) return;

    this.isSubmitting.set(true);

    const dirtyValues = this.getDirtyValues(formGroup);

    if (Object.keys(dirtyValues).length > 0) {
      this.accountsService.updateUser(profile.id, dirtyValues).subscribe({
        next: (result) => {
          void this.authDataService.setUserProfile(result);
          this.isFormEditable.set(false);
        },
        error: () => {
          this.isFormEditable.set(true);
        },
        complete: () => {
          this.isSubmitting.set(false);
          this.cdr.detectChanges();
        },
      });
    } else {
      this.isFormEditable.set(false);
      this.isSubmitting.set(false);
    }
  }

  onEdit() {
    this.isFormEditable.set(true);
  }

  private setupClientSearch(): void {
    this.searchSubject
      .pipe(
        debounceTime(SEARCH_DEBOUNCE_TIME),
        takeUntilDestroyed(this.destroyRef),
        switchMap((searchTerm) => {
          const owner = this.authDataService.getUserProfile()?.owner;
          if (!isDefined(owner) || !hasProperty(owner, 'id')) {
            throw new Error('Owner ID not found');
          }

          const trimmedSearchTerm = searchTerm.trim();

          if (trimmedSearchTerm.length === 0) {
            return of({ items: [] });
          }

          const filters = {
            state: { value: 'active', operator: '=' },
            name: { value: `*${trimmedSearchTerm}*`, operator: '=' },
          };
          const rqlFilter = this.utilityService.constructRqlFilter(filters);

          return this.clientsService.listClients(
            owner.id,
            0,
            CLIENT_SEARCH_LIMIT,
            'name',
            rqlFilter,
          );
        }),
      )
      .subscribe({
        next: (data) => {
          if (isDefined(data) && hasProperty(data, 'items')) {
            this.clientSearchResults.set(
              isNullOrUndefined(data.items) ? [] : data.items,
            );
            this.cdr.detectChanges();
          }
        },
        error: (error) => {
          console.error('Error searching clients:', error);
          this.clientSearchResults.set([]);
        },
      });
  }

  searchClient(searchTerm: string) {
    this.searchSubject.next(searchTerm);
  }
}
