import {
  Component,
  computed,
  inject,
  input,
  output,
  signal,
} from '@angular/core';
import { DataViewModule } from 'primeng/dataview';
import { ConfirmDialogModule } from 'primeng/confirmdialog';
import { ButtonModule } from 'primeng/button';

import { AuthService, UserSession } from '../../../api';
import { TimeAgoPipe } from '../../../shared/pipes/time-ago.pipe';
import { ConfirmationService, MessageService } from 'primeng/api';
import { hasProperty, isDefined, isString } from '../../../shared/utils';

@Component({
  selector: 'app-sessions',
  imports: [DataViewModule, TimeAgoPipe, ConfirmDialogModule, ButtonModule],
  providers: [ConfirmationService, MessageService],
  templateUrl: './sessions.component.html',
  styleUrl: './sessions.component.scss',
})
export class SessionsComponent {
  sessions = input.required<UserSession[]>();
  refreshSessions = output<void>();

  private readonly authService = inject(AuthService);
  private readonly confirmationService = inject(ConfirmationService);

  private readonly refreshTrigger = signal(0);
  readonly timestamp = signal(Date.now());
  protected readonly isLoading = signal(false);

  readonly sessionsList = computed(() => {
    this.refreshTrigger();
    const sessions = this.sessions();
    return isDefined(sessions) ? sessions : [];
  });

  refresh(): void {
    if (this.isLoading()) return;

    this.refreshTrigger.update((v) => v + 1);
    this.timestamp.set(Date.now());
  }

  signOut(session: UserSession): void {
    if (
      !isDefined(session) ||
      !hasProperty(session, 'id') ||
      !isString(session.id)
    ) {
      console.warn('Invalid session data');
      return;
    }

    this.confirmationService.confirm({
      header: 'Confirmation',
      message: 'Are you sure you want to force log out the selected session?',
      closable: true,
      closeOnEscape: true,
      icon: 'pi pi-exclamation-triangle',
      rejectButtonProps: {
        label: 'Cancel',
        severity: 'secondary',
        outlined: true,
      },
      acceptButtonProps: {
        label: 'Ok',
      },
      accept: () => {
        this.isLoading.set(true);
        this.authService.signOut({ session: session.id }).subscribe({
          next: () => {
            this.refreshSessions.emit();
          },
          error: (error) => {
            console.error('Failed to sign out session:', error);
          },
          complete: () => {
            this.isLoading.set(false);
          },
        });
      },
    });
  }
}
