import {
  Component,
  computed,
  input,
  signal,
  effect,
  ChangeDetectorRef,
  inject,
} from '@angular/core';
import {
  AccountsService,
  NotificationActions,
  NotificationSettings,
  UserProfileResponse,
} from '../../../api';
import {
  createDateField,
  createDropdownField,
  FormAction,
  FormFieldConfig,
  GroupValues,
} from '../../../shared/models/form-field.model';
import { GridComponent } from '../../../shared/components/grid/grid.component';
import { DynamicFormComponent } from '../../../shared/components/forms/dynamic-form/dynamic-form.component';
import { GridColumn } from '../../../shared/models';
import { CommonModule } from '@angular/common';
import { FormGroup } from '@angular/forms';
import { AuthDataService } from '../../../core/services/auth-data.service';
import { finalize } from 'rxjs';
import {
  isDefined,
  isNullOrUndefined,
  isObject,
  isString,
} from '../../../shared/utils';

@Component({
  selector: 'app-notifications',
  imports: [DynamicFormComponent, GridComponent, CommonModule],
  templateUrl: './notifications.component.html',
  styleUrl: './notifications.component.scss',
})
export class NotificationsComponent {
  private readonly accountsService = inject(AccountsService);
  private readonly authDataService = inject(AuthDataService);
  private readonly cdr = inject(ChangeDetectorRef);

  userProfile = input<UserProfileResponse | null>();
  protected readonly isFormEditable = signal<boolean>(false);
  protected readonly isSubmitting = signal<boolean>(false);

  readonly columns: GridColumn[] = [
    { field: 'key', header: '', transform: 'uppercase' },
    { field: 'value', header: '', transform: 'capitalize' },
  ];

  readonly settingsDetails = computed(() => {
    const settings = this.userProfile()?.notifySettings;
    if (!settings) return [];

    return [
      { key: 'Allow SMS from', value: settings.smsTime?.from },
      { key: 'Allow SMS to', value: settings.smsTime?.to },
      { key: 'Low Priority', value: this.formatLabel(settings.actions?.low) },
      {
        key: 'Medium Priority',
        value: this.formatLabel(settings.actions?.medium),
      },
      { key: 'High Priority', value: this.formatLabel(settings.actions?.high) },
    ];
  });

  formFields: FormFieldConfig[] = [
    {
      name: 'smsTime',
      key: 'smsTime',
      type: 'group',
      fields: [
        createDateField(
          'from',
          'Allow SMS from',
          this.userProfile()?.notifySettings?.smsTime?.from,
          {
            name: 'from',
            placeholder: 'Select time',
            props: {
              timeOnly: true,
              showSeconds: true,
            },
            style: { width: '100%' },
          },
        ),
        createDateField(
          'to',
          'Allow SMS to',
          this.userProfile()?.notifySettings?.smsTime?.to,
          {
            name: 'to',
            placeholder: 'Select time',
            props: {
              timeOnly: true,
              showSeconds: true,
            },
            style: { width: '100%' },
          },
        ),
      ],
    },
    {
      key: 'actions',
      type: 'group',
      name: 'actions',
      fields: [
        createDropdownField(
          'low',
          'Low Priority',
          Object.values(NotificationActions.LowEnum).map((value) => ({
            label: this.formatLabel(value),
            value,
          })),
          this.userProfile()?.notifySettings?.actions?.low,
          {
            name: 'low',
            props: {
              appendTo: 'body',
            },
          },
        ),
        createDropdownField(
          'medium',
          'Medium Priority',
          Object.values(NotificationActions.MediumEnum).map((value) => ({
            label: this.formatLabel(value),
            value,
          })),
          this.userProfile()?.notifySettings?.actions?.medium,
          {
            name: 'medium',
            props: {
              appendTo: 'body',
            },
          },
        ),
        createDropdownField(
          'high',
          'High Priority',
          Object.values(NotificationActions.HighEnum).map((value) => ({
            label: this.formatLabel(value),
            value,
          })),
          this.userProfile()?.notifySettings?.actions?.high,
          {
            name: 'high',
            props: {
              appendTo: 'body',
            },
          },
        ),
      ],
    },
  ];
  actions: FormAction[] = [
    {
      label: 'Cancel',
      action: () => this.isFormEditable.set(false),
      severity: 'secondary',
    },
    {
      label: 'Save',
      action: (formData: FormGroup) => {
        if (!isDefined(formData)) return;
        this.onSubmit(formData);
      },
      severity: 'primary',
      disabled: (form?: FormGroup) => !isDefined(form) || form.pristine,
    },
  ];

  constructor() {
    effect(() => {
      const settings = this.userProfile()?.notifySettings;
      if (settings) {
        this.updateFormValues<NotificationSettings>(this.formFields, settings);
      }
    });
  }

  private updateFormValues<T>(
    fields: FormFieldConfig[],
    values: T | null | undefined,
  ): void {
    if (isNullOrUndefined(values)) return;

    fields.forEach((field) => {
      if (field.type === 'group' && field.fields) {
        this.updateFormValues(field.fields, values[field.key as keyof T]);
      } else if (field.key && values?.[field.key as keyof T] !== undefined) {
        field.value = values[field.key as keyof T];
      }
    });
  }

  private getDirtyGroupValues(group: FormGroup, path: string): GroupValues {
    if (!isDefined(group) || !isString(path)) return {};

    const formGroup = group.get(path) as FormGroup;
    if (!isDefined(formGroup) || !isDefined(formGroup.controls)) return {};

    return Object.keys(formGroup.controls)
      .filter((key) => {
        const control = group.get(`${path}.${key}`);
        return isDefined(control) && control.dirty;
      })
      .reduce((acc, key) => {
        const control = group.get(`${path}.${key}`);
        if (isDefined(control)) {
          return {
            ...acc,
            [key]: control.value,
          };
        }
        return acc;
      }, {});
  }

  private createUpdatedProfile(
    profile: UserProfileResponse,
    dirtyValues: {
      smsTime: GroupValues;
      actions: GroupValues;
    },
  ): Partial<UserProfileResponse> {
    if (!isDefined(profile) || !isObject(dirtyValues)) return {};

    const hasSmsTimeChanges =
      isObject(dirtyValues.smsTime) &&
      Object.keys(dirtyValues.smsTime).length > 0;
    const hasActionChanges =
      isObject(dirtyValues.actions) &&
      Object.keys(dirtyValues.actions).length > 0;

    return {
      notifySettings: {
        ...profile.notifySettings,
        ...(hasSmsTimeChanges ? { smsTime: dirtyValues.smsTime } : {}),
        ...(hasActionChanges ? { actions: dirtyValues.actions } : {}),
      },
    };
  }

  onSubmit(formGroup: FormGroup): void {
    const profile = this.userProfile();
    if (!isDefined(profile)) return;

    this.isSubmitting.set(true);

    const dirtyValues: {
      smsTime: GroupValues;
      actions: GroupValues;
    } = {
      smsTime: this.getDirtyGroupValues(formGroup, 'smsTime'),
      actions: this.getDirtyGroupValues(formGroup, 'actions'),
    };

    const hasChanges = Object.values(dirtyValues).some(
      (group) => Object.keys(group).length > 0,
    );

    if (!hasChanges) {
      this.isFormEditable.set(false);
      this.isSubmitting.set(false);
      return;
    }

    const updatedProfile = this.createUpdatedProfile(profile, dirtyValues);

    this.accountsService
      .updateUser(profile.id, updatedProfile)
      .pipe(
        finalize(() => {
          this.isSubmitting.set(false);
          this.isFormEditable.set(false);
        }),
      )
      .subscribe({
        next: (result) => {
          void this.authDataService.setUserProfile(result);
        },
      });
  }

  onEdit(): void {
    this.isFormEditable.update((editable) => !editable);
  }

  formatLabel(value?: string | null): string {
    if (!isString(value)) return '';

    switch (value.toLowerCase()) {
      case 'emailsms':
        return 'Email & SMS';
      case 'sms':
        return 'SMS';
      default:
        return value.charAt(0).toUpperCase() + value.slice(1);
    }
  }
}
