import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  inject,
  OnInit,
  signal,
  ViewChild,
} from '@angular/core';
import { AccordionModule } from 'primeng/accordion';
import { DetailsComponent } from '../details/details.component';
import { NotificationsComponent } from '../notifications/notifications.component';
import { SessionsComponent } from '../sessions/sessions.component';
import { AuthService } from '../../../api';
import { AuthDataService } from '../../../core/services/auth-data.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'app-profile',
  imports: [
    AccordionModule,
    DetailsComponent,
    NotificationsComponent,
    SessionsComponent,
  ],
  templateUrl: './profile.component.html',
  styleUrl: './profile.component.scss',
})
export class ProfileComponent implements OnInit {
  private readonly userProfileService = inject(AuthService);
  private readonly authDataService = inject(AuthDataService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  @ViewChild('sessionsComponent') sessionsComponent?: SessionsComponent;

  protected readonly userProfile = this.authDataService.profile;
  protected readonly isLoading = signal(false);

  ngOnInit(): void {
    this.fetchUserProfile();
  }

  fetchUserProfile(): void {
    this.isLoading.set(true);

    this.userProfileService
      .getUserProfile()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (profile) => {
          void this.authDataService.setUserProfile(profile);
        },
        complete: () => {
          this.isLoading.set(false);
          this.cdr.detectChanges();
        },
      });
  }

  onRefresh(): void {
    this.fetchUserProfile();
    this.sessionsComponent?.refresh();
  }
}
